<template>
  <div
    id="settingIndex"
    :style="
      theme
        ? 'background: #0d1117; color: #c9d1d9;'
        : 'background: #faf8ff;color: #32373c;'
    "
  >
    <HeaderInfo />

    <el-divider />

    <!--  -->
    <div class="userForm">
      <div class="info">
        <UserInfo />
      </div>

      <div class="userPhoto">
        <img
          class="userImg"
          src="https://wx1.sinaimg.cn/large/007LmPOmly1go95lrt7nrj30m30m3n5c.jpg"
          @click="$refs['file-inp'].click()"
        >
        <!--  -->
        <div
          class="changePhoto"
          @click="$refs['file-inp'].click()"
        >
          <i class="tyh tyh-pan_icon" />
          
        </div>
        <input
          type="file"
          ref="file-inp"
          accept="image/*"
          hidden
          @change="changePhoto"
        />
      </div>
    </div>

    <!--  -->
    <el-dialog
      title=""
      :visible.sync="photoDialog"
      @opened="onOpenedPhoto"
      @closed="onClosedRemove"
    >
      <div>
        <img
          style="width: 100%; max-height: 270px"
          :src="photoBlob"
          ref="user-photo"
        />
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="photoDialog = false"> </el-button>
        <el-button type="primary" @click="photoDialog = false"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import { mapState } from 'vuex' //  vuex
import UserInfo from './components/UserInfo'
import HeaderInfo from './components/HeaderInfo'
// 
import 'cropperjs/dist/cropper.css'
import Cropper from 'cropperjs'
export default {
  name: 'settingIndex',
  components: {
    UserInfo,
    HeaderInfo
  },
  computed: {
    //  vuex 
    ...mapState(['theme'])
  },
  data () {
    return {
      photoDialog: false, // 
      photoBlob: null // 
    }
  },
  methods: {
    // 
    changePhoto () {
      const file = this.$refs['file-inp']
      const blob = window.URL.createObjectURL(file.files[0])
      this.photoBlob = blob
      this.photoDialog = true
    },
    //  
    onOpenedPhoto () {
      const image = this.$refs['user-photo']
      this.Cropper = new Cropper(image, {
        aspectRatio: 1 / 1,
        viewMode: 1, // 
        dragMode: 'none' // 
      })
    },
    //  
    onClosedRemove () {
      this.$refs['file-inp'].value = ''
      this.Cropper.destroy() // 
    }
  }
}
</script>

